<!-- 搜索页 -->
<template>
  <div class="search">
    <SearchBar @searchVal="searchVal" />
    <div class="content">
      <div class="search-list" v-show="inputVal.length > 0">
				<div class="loading" v-show="isLoadingShow">
					<img src="../assets/images/loading.gif" alt="">
					<p>搜索中...</p>
				</div>
				<div class="empty" v-show="isEmptyShow && searchResultList.length === 0">搜索结果为空</div>
        <ul v-show="!isLoadingShow">
          <li v-for="(item, index) in searchResultList" :key="index" v-html="item"></li>
        </ul>
      </div>
      <div class="history-hot" v-show="inputVal.length === 0">
        <div class="history" v-show="isHistoryShow && searchHistoryList.length">
          <span class="delete-btn" @click="isHistoryShow = false"></span>
          <h4>历史搜索</h4>
          <ul>
            <li v-for="(item, index) in searchHistoryList" :key="index">
							{{item.searchtext}}
						</li>
          </ul>
        </div>
        <div class="hot">
          <h4>热门搜索</h4>
          <ul>
            <li v-for="(item, index) in searchHotList" :key="index">
							{{item.searchtext}}
						</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchBar from "../components/SearchBar";
export default {
  name: "Search",
  components: {
    SearchBar
  },
  data() {
    return {
			inputVal: "",
			isLoadingShow: false,
			isEmptyShow: false,
      isHistoryShow: true,
      allList: ["交易总览", "设计初衷", "块级作用域", "交通银行"],
			searchResultList: [],
			searchHistoryList: [],
			searchHotList: []
    };
  },
  created() {},
  mounted() {
		this.getHistory();
		this.getHot();
	},
  methods: {
		// 获取搜索框值
    searchVal(val) {
			this.inputVal = val;
      if (this.inputVal) {
				this.isLoadingShow = true;
				this.isEmptyShow = false;
				const reg = new RegExp(this.inputVal, "g");
				this.$axios.post('/hope3/hopeModule/searchMoudleByName', {
					"modulename": this.inputVal
				}).then(res => {
					this.isLoadingShow = false;
					if (res.status === 200 && res.data.status === '000000') {
						const resData = res.data.data || [];
						if (resData.length === 0) this.isEmptyShow = true;
						// 关键字高亮
						this.searchResultList = resData.map(i => {
							return i.modulename.replace(reg, "<span style='color: #45A4B1;'>" + this.inputVal + "</span>");
						});
					} else {
						this.isEmptyShow = true;
					}
				}).catch(err => {
					this.isLoadingShow = false;
					this.isEmptyShow = true;
				});
				// const searchResultList = this.allList.filter(item => item.indexOf(this.inputVal) !== -1);
      }
		},
		// 获取搜索历史
		getHistory() {
			this.$axios.post('/hope3/hopeSearchHistory/querySearchRecord', {
				"aamid": '1'
			}).then(res => {
				if (res.status === 200 && res.data.status === '000000') {
					const resData = res.data.data || [];
					this.searchHistoryList = [...resData];
				}
			});
		},
		// 获取热门推荐
		getHot() {
			this.$axios.post('/hope3/hopeSearchHistory_h/queryHotSearch', {
				"aamid": '1'
			}).then(res => {
				if (res.status === 200 && res.data.status === '000000') {
					const resData = res.data.data || [];
					this.searchHotList = [...resData];
				}
			});
		}
  }
};
</script>
<style lang="less" scoped>
.search {
  min-height: 100%;
  background: #fff;
}
.content {
  .search-list {
		.loading{
			padding-top: 1.2rem;
			text-align: center;
			font-size: 0.14rem;
			color: #666666;
			img{
				width: 0.4rem;
			}
		}
		.empty{
			padding-top: 1.2rem;
			text-align: center;
			font-size: 0.14rem;
			color: #666666;
		}
    li {
      height: 0.4rem;
      line-height: 0.4rem;
      font-size: 0.14rem;
      color: #232323;
      border-bottom: 1px solid #eaeaea;
      box-sizing: border-box;
      padding: 0 0.17rem;
    }
  }
  .history-hot {
    padding: 0.1rem 0.16rem 0 0.16rem;
    h4 {
      font-size: 0.14rem;
      height: 0.2rem;
      line-height: 0.2rem;
      color: #232323;
      margin-bottom: 0.12rem;
    }
    ul {
      overflow: hidden;
      li {
        font-size: 0.13rem;
        color: #666666;
        padding: 0.06rem 0.15rem;
        background: #f1f1f1;
        border-radius: 0.15rem;
        float: left;
        margin-right: 0.06rem;
        margin-bottom: 0.06rem;
      }
    }
    .history {
      position: relative;
      margin-bottom: 0.18rem;
      .delete-btn {
        position: absolute;
        width: 0.22rem;
        height: 0.22rem;
        display: block;
        top: -0.02rem;
        right: -0.04rem;
        background: url("../assets/images/icon_delete@2x.png") no-repeat center
          center;
        background-size: 0.22rem 0.22rem;
      }
    }
  }
}
</style>