<!-- 搜索栏组件 -->
<template>
  <div class="search-bar">
		<div class="back" @click="back"></div>
		<div class="search-input">
			<input type="text" @input="inputHandle(vm, $event)" />
		</div>
		<slot></slot>
	</div>
</template>

<script>
import { debounce } from '../assets/js/utils';
export default {
  data() {
    return {
			vm: this
		};
  },
  mounted() {
		
	},
	methods: {
		inputHandle: debounce((vm, e) => {
			const value = e.target.value.trim();
			vm.$emit('searchVal', value);
		}, 500),
		back() {
			this.$router.go(-1);
		}
	}
};
</script>
<style lang="less" scoped>
	.search-bar{
		height: 0.5rem;
		display: flex;
		align-items: center;
		border-bottom: 1px solid #EAEAEA;
		padding-right: 0.1rem;
		background: #fff;
		.back{
			width: 0.4rem;
			height: 100%;
			background: url('../assets/images/icon_return@2x.png') no-repeat center center;
			background-size: 0.2rem 0.2rem;
		}
		.search-input{
			flex: 1;
			input{
				width: 100%;
				display: block;
				box-sizing: border-box;
				padding: 0.05rem 0.1rem 0.05rem 0.4rem;
				background: #F1F1F1 url('../assets/images/icon_search@2x.png') no-repeat 0.12rem center;
				background-size: 0.2rem 0.2rem;
				border-radius: 0.15rem;
				height: 0.3rem;
				line-height: 0.2rem;
				font-size: 0.14rem;
				color: #000;
			}
		}
	}
</style>